body {
  background-color: #ede8e2 !important;
}

.img-banner {
  width: 144pt;
  padding-top: 10pt;
  margin-bottom: 15pt;
}

.center-item {
  width: 100%;
  display: flex;
  justify-content: center;
}

.container-banner {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.column {
  flex: 1;
  color: white;
  background-color: red;
  height: 100vh;
}

.card-img-overlay {
  background: linear-gradient(to top, black, transparent) !important;
}

.size-btn {
  width: 25px;
  height: 25px;
  display: flex !important;
  justify-content: center;
  align-items: center;
  font-size: 16pt !important;
  font-weight: bold;
  background-color: #ede8e2 !important;
  color: #b53924 !important;
  border-radius: 0 !important;
}

.no-image__content {
  background-color: #b53924;
  height: 103px;
}

.no-image__content-overlay {
  position: absolute !important;
  top: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  left: 0 !important;
  padding: 1.25rem !important;
}

.img-litros {
  width: 92pt;
  height: 92pt;
}